let browserFonts = ["Times New Roman", "Helvetica"];

// Note that imported webfonts are currently hard-coded in styles/styles.scss. If you want to add more webfonts, edit the import statement there too.
let webFonts = ["Open Sans", "Roboto", "Slabo 27px", "Lato", "Oswald"];

export default browserFonts.concat(webFonts);



